*{
    font-family: 'Ubuntu', sans-serif;
    background-color: #f6f7f9;
}
.header{
    display: flex;
    justify-content: space-evenly;
    
    div:first-child{
        text-align: center;
        border-right: 1px solid black;
        padding: 10px;
        width: 50%;
    }
    div{
        margin: 10px;
    }
    form{
        display: grid;
    }
    form input{
        border-radius: 10%;
        border: solid 1px rgb(2, 2, 77);
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    }
    width: 100%;
}
.resultref{
    div:first-child{
        text-align: center;    
    }
    display: flex;
    justify-content: center;
    margin: 10%;
    border: solid 1px rgb(17, 14, 14);
    border-radius: 10px;
    padding: 2%;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.buttonref{
    text-align: center;
    display: grid;
}
.ligneRef{
    display: inline-flex;
    text-align: center;
    width: 100%;
    background-color: rgba(77, 70, 70, 0.158);
    border-radius: 20px 0px 0px 20px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    p{
        padding: 2%;
        background-color: rgba(77, 70, 70, 0.158);
        text-align: center;
    }
}
.regerror{
    color: aliceblue;
background-color: rgb(0, 182, 70);
text-align: center;
}
.encours{
